/* Mozila Firefox */
/* Slick Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  --webkit-touch-callout: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-slider .slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-slider .slick-list:focus {
  outline: none;
}
.slick-slider .slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-slider .slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}
.slick-loading .slick-slider .slick-track {
  visibility: hidden;
}
.slick-slide {
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-slider .slick-dots {
  position: relative;
  width: 100%;
  margin: 0.9375rem 0;
  padding: 0;
  text-align: center;
}
.slick-slider .slick-dots li {
  display: inline-block;
  vertical-align: middle;
  list-style: none;
  line-height: 0;
  padding-left: 5px;
  padding-right: 5px;
}
.slick-slider .slick-dots li button {
  width: 20px;
  height: 20px;
  padding: 0;
  border: 2px solid var(--secondary-100);
  border-radius: 100%;
  font-size: 0;
  background-color: var(--primary-100);
  transition: background-color ease 0.3s, opacity ease 0.3s;
  outline: none;
}
.slick-slider .slick-dots li button:focus, .slick-slider .slick-dots li button:hover {
  outline: none;
  cursor: pointer;
  background-color: var(--secondary-100);
  opacity: 0.8;
}
.slick-slider .slick-dots li.slick-active button {
  background-color: var(--secondary-100);
}

.slick-arrow {
  --sArrowWidth: 20px;
  position: absolute;
  top: calc(50% - var(--sArrowWidth) / 2);
  padding: 0;
  width: var(--sArrowWidth);
  height: var(--sArrowWidth);
  font-size: 0;
  background-color: transparent;
  box-shadow: none;
  border: calc(var(--sArrowWidth) / 4) solid var(--primary-100);
  border-top: 0;
  border-right: 0;
  transition: border-color ease 0.3s;
  z-index: 9;
}
.slick-arrow:focus, .slick-arrow:hover {
  outline: none;
  cursor: pointer;
  border-color: var(--secondary-100);
}
.slick-arrow.slick-prev {
  left: 0;
  border-top: 0;
  border-right: 0;
  transform: rotate(45deg);
}
.slick-arrow.slick-next {
  right: 0;
  transform: rotate(-135deg);
}
.slick-arrow.slick-hidden {
  display: none;
}

/* Reset css
   ========================================================================== */
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
a,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  font-size: inherit;
  background: transparent;
  border: 0;
  outline: none;
  -webkit-font-smoothing: subpixel-antialiased;
  -webkit-tap-highlight-color: rgba(var(--black), 0);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

article,
aside,
footer,
header,
nav,
section,
figcaption,
figure,
main {
  display: block;
}

/* Text-level semantics
   ========================================================================== */
sub,
sup {
  position: relative;
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content
   ========================================================================== */
audio,
video,
iframe {
  display: block;
  max-width: 100%;
}

