  transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
  cursor: pointer;
  border-radius: 2px;
}
.hamburger span:before, .hamburger span:after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: var(--primary-100);
  transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
  cursor: pointer;
  border-radius: 2px;
}
.hamburger span:before {
  top: -8px;
}
.hamburger span:after {
  bottom: -8px;
}
.hamburger.is-clicked span {
  background-color: transparent !important;
}
.hamburger.is-clicked span:before, .hamburger.is-clicked span:after {
  top: 0;
  background-color: var(--primary-100);
}
.hamburger.is-clicked span:before {
  transform: rotate(45deg);
}
.hamburger.is-clicked span:after {
  transform: translateY(-6px) rotate(-45deg);
  top: 6px;
}

.mbnav {
  position: relative;
  display: none;
  z-index: 999;
}
@media (max-width: 991px) {
  .mbnav {
    display: block;
  }
}
.mbnav__state {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 100%;
  width: 100%;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  z-index: 999;
  pointer-events: none;
}
.mbnav__inner {
  position: relative;
  max-height: 100vh;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.mbnav__top {
  display: flex;
  justify-content: space-between;
  background: rgba(var(--secondary-100), 1);
}
.mbnav__logo {
  color: var(--white);
  padding: 15px;
}
.mbnav .menu-wrap {
  --leftSlide: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: var(--leftSlide);
  transition: 0.4s ease;
}
.mbnav .menu-wrap .menu-wrap {
  right: -100%;
  opacity: 0;
  visibility: hidden;
}
.mbnav .menu-wrap > .menu-inner {
  max-height: 100%;
  padding-top: 5.3125rem;
  padding-bottom: 1.25rem;
  background-color: var(--white);
  pointer-events: auto;
  overflow: hidden auto;
}
.mbnav ul {
  position: static;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0 20px;
  transition: 0.4s ease;
}
.mbnav ul li.is-open > .menu-wrap {
  opacity: 1;
  visibility: visible;
}
.mbnav ul li:not(.btn-wrap) {
  position: static;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
  margin: 0 0 0 0;
  list-style: none;
  color: var(--primary-100);
  font-size: 1.25rem;
  font-weight: 500;
  border-bottom: 1px solid var(--gray);
}
.mbnav ul li:not(.btn-wrap):last-child {
  border-bottom: 0;
}
.mbnav ul li:not(.btn-wrap)::before {
  display: none;
}
.mbnav ul li:not(.btn-wrap).has-sub a::after {
  display: none;
}
.mbnav ul li:not(.btn-wrap) .mbnav__caret::before, .mbnav ul li:not(.btn-wrap) a::after {
  content: "";
  --sArrowWidth: 10px;
  display: flex;
  align-items: center;
  margin: auto;
  width: var(--sArrowWidth);
  height: var(--sArrowWidth);
  border: calc(var(--sArrowWidth) / 4) solid var(--primary-100);
  border-left: 0;
  border-bottom: 0;
  transform: rotate(45deg);
}
.mbnav ul li:not(.btn-wrap) a {
  position: relative;
  display: block;
  padding: 10px 0;
  color: currentColor;
  line-height: 1.3;
  text-decoration: none !important;
  z-index: 2;
  cursor: pointer;
  flex: 1 1 auto;
  order: -1;
}
.mbnav ul li:not(.btn-wrap) a::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  margin-left: auto;
  margin-right: 0;
  display: none;
}
.mbnav__caret {
  display: grid;
  align-items: center;
  justify-content: flex-end;
  width: 30px;
  height: auto;
  cursor: pointer;
  z-index: 1;
}
.mbnav__caret:not(.trigger-caret) {
  pointer-events: none;
}
.color-mbnav .mbnav__caret::before {
  color: var(--primary-100) !important;
}

.mbnav ul li:not(.btn-wrap).is-open > .mbnav__caret:after {
  transform: rotate(0);
}
.mbnav ul li:not(.btn-wrap).has-sub > a {
  flex: 1 1 auto;
}
.mbnav ul li:not(.btn-wrap) .btn-wrap {
  position: fixed;
  bottom: 20px;
  left: 20px;
  width: calc(100% - 40px);
  margin: 0;
  transform: translateY(200%);
  transition: 0.4s ease-in-out;
}
.mbnav ul li:not(.btn-wrap) .btn-wrap::before {
  display: none;
}
.mbnav ul li:not(.btn-wrap) .btn-wrap a {
  display: block;
}
.mbnav ul ul li {
  font-size: 16px;
}
.mbnav ul ul li a {
  padding-left: 25px;
}
.mbnav ul ul li li {
  font-size: 14px;
}
.mbnav ul ul li li a {
  padding-left: 35px;
}
.mbnav ul > li > .sub-menu {
  position: absolute;
  left: 100%;
}
.mbnav ul .back-click {
  width: 100%;
  order: -1;
  color: currentColor;
  padding: 15px 0;
  font-weight: 400;
}
.mbnav ul .back-click::before {
  content: "a";
  font-family: "iconfontfamily" !important;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  text-transform: none;
  position: static;
  display: inline-block;
  width: auto;
  height: auto;
  color: var(--primary-100);
  margin-right: 15px;
  margin-bottom: 3px;
  font-size: 18px;
  border: 0;
}
.mbnav__backdrop {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  visibility: hidden;
  transition: all 0.4s ease-in;
  background: rgba(var(--black), 0.55);
  opacity: 0;
  z-index: -1;
}
.mbnav__overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  transition: all 0.4s ease-in;
  background: rgba(var(--black), 0.5);
  opacity: 0;
  visibility: hidden;
  z-index: 99;
}
.mbnav__overlay.is-show {
  opacity: 1;
  visibility: visible;
}
.mbnav.is-open .btn-wrap {
  transform: translateY(0);
}
.mbnav.is-open .mbnav__state {
  left: 0;
}
.mbnav.is-open .mbnav__backdrop {
  opacity: 1;
  z-index: 999;
  visibility: visible;
}

